.blog-container {
  margin: auto;
  margin-top: 80px;
  z-index: 10; }
  @media screen and (max-width: 768px) {
    .blog-container {
      margin-bottom: 50px; } }
  .blog-container .blog-aeroplane {
    margin-top: -15px;
    position: absolute;
    z-index: 20;
    width: 110px;
    transform: translateX(-10px) translateY(-10px); }
    @media screen and (min-width: 768px) {
      .blog-container .blog-aeroplane {
        width: 150px; } }
  .blog-container .blog-main {
    margin-bottom: 50px; }
  .blog-container .sub-container {
    height: 100%; }
  .blog-container .blog-image {
    justify-content: center; }
    @media screen and (max-width: 768px) {
      .blog-container .blog-image {
        margin-top: 30% !important; } }
    .blog-container .blog-image .center-align {
      margin-top: 124px; }
      @media screen and (max-width: 768px) {
        .blog-container .blog-image .center-align {
          margin-top: 0 !important; } }
    .blog-container .blog-image .blog-front-image {
      z-index: -10;
      width: 80vw;
      margin-top: -100px; }
      @media screen and (max-width: 1000px) {
        .blog-container .blog-image .blog-front-image {
          width: 100vw !important; } }
