p {
  margin-bottom: 5px;
}

.blog-container {
  display: flex;
  flex-wrap: wrap;
}

.blog-title {
  color: #46737a;
  font-weight: 400;
}

.blog-title:hover {
  color: #21a089;
}

.blog-date {
  color: #99bbc1;
  font-size: 10px;
}

.blog-img {
  object-fit: cover;
  border-radius: 6px;
  width: 100%;
  box-shadow: 0 10px 50px 0 rgba(70, 115, 122, 0.15);

  @media screen and(min-width: 768px) {
    width:90%;
  }
}

.blog-img-main {
  width: 100%;

  @media screen and(min-width: 768px) {
    height: 300px;
  }
}

.blog-img-sub {
  height: 180px;
}

.blog-main-title {
  font-size: 22px;
}

.blog-text {
  font-size: 12px;
  color: #46737a;
}
.blog-text-link {
  font-weight: 300;
  color: #00d7af;
}
.blog-main-sub {
  font-size: 16px;
}

.blog-main {
  display: flex;
  flex-wrap: wrap;
  padding: 0 15px;
  div{
    padding:0;
  }
}

.blog-main-container {
  margin: 10px 0;
}

.blog-sub-container {
  margin-top: 10px;
  margin-bottom: 10px;
}

@media (min-width: 992px) {
  .blog-img-main {
    height: 300px;
  }
}
